import React, { useEffect, useState } from 'react'
import {
  Card,
  CardBody,
  Row,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  CardHeader,
  CardTitle
} from 'reactstrap'
import classnames from 'classnames'
import { User, Info } from 'react-feather'
import AccountTab from './Account'
import InfoTab from './Information'
import { FormattedMessage } from 'react-intl'
const GeneralInfo = (props) => {
  const [activeTab, setActiveTab] = useState('terms-and-condition')

  useEffect(() => setActiveTab(props.activeTab), [props.activeTab])

  return (
    <Row>
      <Col sm='12'>
        <Card>
          <CardHeader>
            <CardTitle>
              <FormattedMessage id={'setting.generalInformation'} />
            </CardTitle>
          </CardHeader>
          <CardBody className='pt-2'>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: activeTab === 'terms-and-condition'
                  })}
                  onClick={() => {
                    setActiveTab('terms-and-condition')
                  }}
                >
                  <User size={16} />
                  <span className='align-middle ml-50'>
                    <FormattedMessage id='setting.accountInformation' />
                  </span>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: activeTab === 'privacy-policy'
                  })}
                  onClick={() => {
                    setActiveTab('privacy-policy')
                  }}
                >
                  <Info size={16} />
                  <span className='align-middle ml-50'>
                    <FormattedMessage id='setting.changePassword' />
                  </span>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: activeTab === 'language'
                  })}
                  onClick={() => {
                    setActiveTab('language')
                  }}
                >
                  <User size={16} />
                  <span className='align-middle ml-50'>
                    <FormattedMessage id='setting.accountInformation' />
                  </span>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: activeTab === 'contact'
                  })}
                  onClick={() => {
                    setActiveTab('contact')
                  }}
                >
                  <Info size={16} />
                  <span className='align-middle ml-50'>
                    <FormattedMessage id='setting.changePassword' />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId='terms-and-condition'>
                <AccountTab />
              </TabPane>
              <TabPane tabId='privacy-policy'>
                <InfoTab />
              </TabPane>
              <TabPane tabId='language'>
                <AccountTab />
              </TabPane>
              <TabPane tabId='contact'>
                <InfoTab />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  )
}
export default GeneralInfo
