import React from 'react'
import ScrollToTop from 'react-scroll-up'
import { Button } from 'reactstrap'
import * as Icon from 'react-feather'
import classnames from 'classnames'
import { FormattedMessage } from 'react-intl'
import { getExternalAppUrl, IMAGE } from '../../../configs/app-configs'
import AppId from '../../../configs/appId'
import useWindowDimensions from '../../../customHooks/useWindowDimensions'
import { MAX_MOBILE_WIDTH } from '../../../configs/app-configs'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'

const Footer = (props) => {
  const { width } = useWindowDimensions()
  const history = useHistory()
  const navConfigs = useSelector((state) => [...state.navbar.navConfigs])

  const goToPage = (e, name) => {
    e.preventDefault()
    let currentRoute = navConfigs.find((item) => item.code === name)
    if (!currentRoute) {
      currentRoute = {
        isExternalApp: AppId.APP_NO1 === props.AppId,
        navLink: ''
      }
    }
    if (!currentRoute.isExternalApp) {
      history.push(`${currentRoute.navLink}`)
    } else {
      window.location.href = getExternalAppUrl(currentRoute.appId, currentRoute.navLink);
    }
  }

  return (
    <footer>
      <div
        className={classnames('footer footer-light', {
          'd-none': width < MAX_MOBILE_WIDTH
        })}
      >
        <div className='d-flex justify-content-between'>
          <div className='float-md-left d-block d-md-inline-block mt-25'>
            <div>
              <FormattedMessage id='footer.copyRight' />
            </div>
            <div>
              <FormattedMessage id='footer.companySlogan' />
            </div>
          </div>
          <div className='float-md-right d-none d-md-block'>
            <a
              className='mr-1'
              href='https://www.apple.com/app-store/'
              target='_blank'
            >
              <img src={IMAGE.DOWNLOAD_APP_IOS} alt='DOWNLOAD ON APP STORE' />
            </a>
            <a href='https://play.google.com/store/apps' target='_blank'>
              <img src={IMAGE.DOWNLOAD_APP_ANDROID} alt='DOWNLOAD ON APP I' />
            </a>
          </div>
        </div>
      </div>
      <div
        className={classnames('footer footer-light footer-mobile text-center', {
          'd-none': width >= MAX_MOBILE_WIDTH
        })}
      >
        <div className='w-25'>
          <a href='#' onClick={(e) => goToPage(e, 'home')}>
            <Icon.Home />
            <div className='mt-1'>Trang chủ</div>
          </a>
        </div>
        <div className='w-25'>
          <a href='#' onClick={(e) => goToPage(e, 'contract/management')}>
            <Icon.List />
            <div className='mt-1'>Hợp đồng</div>
          </a>
        </div>
        <div className='position-relative w-25'>
          <a href='#' onClick={(e) => goToPage(e, 'buy-insurance')}>
            <img src={IMAGE.BUY_INSURANCE} className='buy-insurance' alt='' />
            <Icon.PlusCircle style={{ visibility: 'hidden' }} />
            <div className='mt-1'>Mua bảo hiểm</div>
          </a>
        </div>
        <div className='w-25'>
          <a href='#' onClick={(e) => goToPage(e, 'home')}>
            <Icon.Gift />
            <div className='mt-1'>Khuyến mại</div>
          </a>
        </div>
        <div className='w-25'>
          <a href='#' onClick={(e) => goToPage(e, 'contact')}>
            <Icon.MessageSquare />
            <div className='mt-1'>Liên hệ</div>
          </a>
        </div>
      </div>
      {props.hideScrollToTop === false ? (
        <ScrollToTop showUnder={160}>
          <Button color='primary' className='btn-icon scroll-top d-none d-md-block'>
            <Icon.ArrowUp size={15} />
          </Button>
        </ScrollToTop>
      ) : null}
    </footer>
  )
}

export default Footer
